.notification-container {
    width: 100%;  
    height: calc(100vh - 140px);
    overflow-y: scroll;
}
.noti-content {
    margin: 20px 30px; 
    display: flex;
    width: calc(100% - 60px); 
}
.noti-content-left {
    flex: 1;
}
.noti-content-right {
    flex: 1;
    height: 500px;
    background: white;
    border-radius: 4px;
    margin: 20px 0px;
    margin-left: 20px;
}
.noti-box {
    background: white;
    padding: 20px 15px;
    width: 100%;
    margin: 20px 5px;
    border-radius: 4px; 
    opacity: 0.87;
}
.noti-box:nth-child(1) {
    margin-top: 20px;
}
.noti-box-top {
    display: flex;
    margin-bottom: 20px;
    justify-content: space-between;
    align-items: center;
}
.noti-box p{
    font-size: 11px;
    margin: 5px 0px;
}

.noti-box-top p {
    font-size: 12px;
}