 
.variant-image-container {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
}
.variant-image-container h5 {
    opacity: 0.8;
    font-weight: 500;
    width: 80px;
}
.variant-img-content {
    width: 80px;
    margin: 12px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;
    background: #F0F2F8 0% 0% no-repeat padding-box;
}
.variant-img-content .variant-add-image-icon {
    width: 18px;
    margin-bottom: 6px;
}
.variant-img-content p {
    font-size: 10px;
    opacity: 0.8;
}
.variant-img-content span {
    position: absolute;  
    top: -2px;
    right: -2px;   
    background: red;
    color: white;
    border-radius: 100px;
    padding: 0px 5px;                                                                                                                     
}
.variant-img-content span:hover {
    cursor: pointer;
}
.variant-main-multi-img-input {
    width: 80px;
    height: 80px;
    position: absolute;
    opacity: 0;
}
.main-multi-img-input:hover {
    cursor: pointer;
}
.variant-img-content .variant-img-content-img {
    max-width: 70px;
    max-height: 70px;   
}
.main-img-content-border {
    border: 1px solid rgba(51, 51, 51, 0.2);
    border-radius: 4px;
}