
.sign-in{
    width: 500px;
    display: flex;
    flex-direction: column;
}
.sign-in-container {
    padding: 10px;
}
.sign-in-content h1 {
    font-size: 18px;
    color: #333;
}
.form-icon-grp {
    position: relative; 
    height: 40px;
    flex: 0;
}
.form-icon {
    position: absolute;
    top: 7px;
    right: 4px;
}
.login-form .form-group input
.login-form .form-group .form-icon-grp input {
    font-size: 14px;
}
.text-danger {
    font-size: 12px;
    color: red;
}.link {
    font-weight: 700;
    color: #0F5EEA;
    font-size: 14px;
}
.button-blue {
    background: #0F5EEA;   
    padding: 10px 25px;
    border-radius: 2px;
    color: white;
    border: none;
} 