.edit-stock-price {
    width: 500px;
    height: auto;
}
.edit-stock-price-container {
    display: flex;
    width: 400px;
    flex-direction: column;
}
.edit-stock-price-container h1 {
    font-size: 16px;
    margin-bottom: 20px;
}
.e-s-p-content {
    display: flex; 
    align-items: center;
    width: 100%;
    margin: 10px 0px;
}
.e-s-p-content label {
    font-size: 14px;
    color: #333;
    flex: 1;
}
.e-s-p-content input {
    flex: 2;
    border-radius: 2px;
    height: 35px;
    font-size: 14px; 
    text-indent: 10px;
    outline: none;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #00000033
}
.e-s-p-content input:focus {
    outline: none;
}
.e-s-p-content div {
    flex: 2;
}

@media only screen and (max-width: 450px) {
    .edit-stock-price-container {
        width: 300px; 
    }
    .modal-md-content {
        padding: 20px;
    }
}

@media only screen and (max-width: 350px) {
    .edit-stock-price-container {
        width: 250px; 
    }
    .modal-md-content {
        padding: 20px;
    }
}