.tag-form-container{
    display: flex;
    align-items: center;
    margin: 10px 0px;
    justify-content: center;
}
.tag-form-container label {
    flex: 1;
    font-size: 14px;
}
.tag-form-content {
    flex: 6;
    display: flex;
    flex-direction: column;
}
.tag-form-container-div {
    width: 100%;
    position: relative;
}
.tag-form-container input { 
    width: 100%;
    outline: none;
    height: 40px;
    text-indent: 10px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #D8DCE6;
    border-radius: 4px;
    opacity: 1;
}
.tag-container {
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translate(0%,-50%);
    display: flex;
}
.tag {
    border: 1px solid #0F5EEA;
    margin: 0px 5px;
    border-radius: 19px;
    display: flex;
    width: auto;
    align-items: center;
    justify-content: space-between; 
    font-size: 11px;
    padding: 3px 5px; 
    opacity: 1;
}
.tag span {
    background: #0F5EEA;
    color: white;
    padding: 3px 6px;
    border-radius: 100px;
    padding-top: 2px;
    margin-left: 4px;
}
.tag span:hover {
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .tag-form-container {
        flex-direction: column;
        align-items: flex-start;
    }
    .tag-form-container-div {
        margin-top: 5px;
        width: 100%;
    }   
}