.top-bar-right {
    height: 70px;
    display: flex;
    background: white;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    margin: 0 auto; 
    flex-direction: row; 
}
.top-bar-right h5 {
    font-size: 16px;
    margin: 0px 10px;
    letter-spacing: 0.28px;
    color: #000000;
    opacity: 1;
}
.nav-btns {
    display: flex; 
    align-items: center;
}
.nav-btns img {
    width: 25px;
    margin: 0px 10px;
}
.nav-btns img:nth-child(2) {
    width: 22px;
}
.nav-btn {
    margin: 0px 10px;
    background: #0F5EEA1C 0% 0% no-repeat padding-box;
    border-radius: 26px;
    opacity: 1;
    padding: 10px 15px;
    font-size: 14px;
    letter-spacing: 0.27px;
    color: #0F5EEA;
    opacity: 1;
}
.mobile-view-nav {
    display: none;
}
@media only screen and (max-width:700px) {
    .nav-btns {
        display: none;
    }
    .mobile-view-nav {
        display: flex;
    }
    .top-bar-right {
        height: 40px;
    }
}
@media only screen and (max-width: 400px) {
    .top-bar-right h5  {
        font-size: 14px;
    }
}