* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto;
}
@font-face {
    font-family: Roboto;
    src: url('./assets/fonts/Roboto/Roboto-Regular.ttf');
}
@font-face {
    font-family: Quicksand;
    src: url('./assets/fonts/Quicksand/static/Quicksand-Regular.ttf');
}
p {
    margin: 0;
}
button:hover ,
a,
.hover {
    cursor: pointer;
}
.d-flex {
    display: flex;
}
.flex-row {
    flex-direction: row;
}
.flex-col {
    flex-direction: column;
}
.a-i-c {
    align-items: center;
}
.j-c-b {
    justify-content: space-between;
}
.j-c-c {
    justify-content: center;
}
.j-c-a {
    justify-content: space-around;
}
.j-c-s {
    justify-content: flex-start;
}
.j-c-e {
    justify-content: flex-end;
}
.text-blue {
    color: #0F5EEA;
}
.text-grey {
    color: #333;
}
.text-conf {
    font-size: 14px;
}
.text-center {
    text-align: center;
}
.bg-blue {
    background: #0F5EEA;
}
.bg-light-blue {
    background: #0F5EEA1C 0% 0% no-repeat padding-box;
}
.not-found {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-blue {
    background: #0F5EEA;
    padding: 10px 15px;
    color: white;
    font-size: 14px;
    outline: none;
    border: none;
    border-radius: 2px;
}
.cursor:hover {
    cursor: pointer;
}
.disabled {
    filter: grayscale(100%);
    opacity: 0.5;
}
.font-13 {
    font-size: 13px;
}
.decor-none {
    text-decoration: none;
}
.decor-none:hover {
    text-decoration: none;
}
@media only screen and (max-width: 400px) {
    .btn-blue {
        width: 110px;
        font-size: 12px;
    }
}