
.variant-content { 
    min-height: 100px;
    width: 100%;
    margin: 10px 0px;
    display: flex;
    flex-direction: row; 
    border-radius: 4px;
    background: white;
    opacity: 1;
}
.variant-content:nth-child(1) {
    margin-top: 5px;
}

.variant-col-1 {
    margin: 10px;
    display: flex;
    align-items: flex-start;
    flex: 1;
}
.variant-img {
    width: 50px;
    margin: 0px 10px;
}
.variant-col-2 {
    display: flex;
    flex: 15; 
    width: 100%;
    margin: 10px; 
    flex-direction: column; 
    justify-content: space-around;
}
.variant-row-1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
    align-items: center;
}
.variant-row-1 h5 {
    font-weight: 500;
    font-size: 14px;
    text-transform: capitalize;
    color: #000;
    opacity: 0.8;
}
.variant-row-2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.variant-row-2-content {
    display: flex;
    width: 100%;
    flex-direction: row;
}
.variant-spec {
    width: 120px;
    margin-right: 20px;
    border-right: 1px solid rgba(51, 51, 51, 0.2);
}
.variant-spec:last-child {
    border: none;
}
.variant-key {
    font-size: 12px;
    margin-bottom: 10px;
    color: rgb(107, 107, 107);
}
.variant-value {
    font-size: 12px;
    margin-top: 10px;
}
.btn-outline {
    border: 1px solid rgba(15, 95, 234, 0.9);
    outline: none;
    border-radius: 4px;
    color: rgba(15, 95, 234, 0.9);
    background: white;
    padding: 10px 15px;
    margin: 0px 10px;
}
.btn-primary {
    background: rgba(15, 95, 234);
    outline: none;
    border-radius: 4px;
    color: white; 
    border: none;
    padding: 10px 15px;
    margin: 0px 10px;
}
.variant-col-3 {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    align-items: flex-end;
    justify-content: space-around;
}
.variant-col-3 .variant-form-group  {
    display: flex;
    align-items: center;
    margin: 2px 0px;
}
.variant-col-3 .variant-form-group label{
    flex: 2; 
    text-align: right;
    font-size: 13px;
    margin-right: 10px;
}
.variant-col-3 .variant-form-group div {   
    width: 130px;
    display: flex; 
    align-items: flex-start;
    flex-direction: column;
}
.variant-col-3 .variant-form-group div .variant-form-control{
    outline: none;
    width: 130px;
    height: 40px; 
    text-indent: 10px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #D8DCE6;
    border-radius: 4px;
}

@media only screen and (max-width: 1030px) {
    .btn-outline,
    .btn-primary {
        padding: 12px 10px;
    }
    .btn-outline {
        padding: 10px 10px;
    }
    .variant-row-2-buttons {
        display: flex;  
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-end;
    }
    .variant-row-2-buttons button {
        margin: 5px 0px;
        width: 140px;
    }
    .variant-content {
        padding: 15px 0px;
    }
    .variant-spec {
        width: 120px;
    }
}

@media only screen and (max-width: 900px) {
    .variant-content {
        padding: 10px 0px;
    }
    .variant-col-2 {
        margin: 0px;
    }
    .variant-col-1 {
        margin-left: 5px;
        margin-right: 5px;
    }
    .variant-spec {
        width: 100px;
        margin-right: 10px;
        border-right: 1px solid rgba(51, 51, 51, 0.2);
    }
    .variant-col-1 span {
        padding-right: 0px;
        padding-left: 0px;
    }
    .variant-row-2-buttons button {
        margin: 5px 0px;
        width: 120px;
    }
}

@media only screen and (max-width: 500px) {
    .variant-col-1 span {
        display: none;
    }
    .variant-col-1 {
        width: 100%;
    }
    .variant-col-2 {
        width: 100%;
    }
    .variant-col-3 {
        width: 100%;
        margin: 0 auto;
        margin-top: 20px;
        flex-direction: row;
        justify-content: space-between;
    }
    .variant-content {
        flex-wrap: wrap;
    }
    .variant-row-2-buttons {
        flex-direction: row;
    }
    .variant-row-2-buttons button {
        margin: 0;
        margin: 0px 10px;
        width: auto;
    }
    .variant-spec {
        margin-top: 10px;
        width: 30%; 
    } 
    .variant-img {
        position: absolute;
    }
    .variant-col-1 {
        position: absolute;
    }
    .variant-content {
        position: relative;
    }
    .variant-col-2 {
        width: 100%;
    }
    .variant-row-1 {
        margin: 20px 0px;
        margin-left: 60px;
    }
    .variant-row-2-content {
        width: 100%;
    }
}

@media only screen and (max-width: 400px) {
    .variant-spec {
        margin-top: 10px;  
        padding-right: 10px;
        width: auto; 
    } 
    .variant-row-2-content {
        justify-content: space-between;
    }
    .variant-row-2-buttons button {
        margin: 0;
        margin: 0px 5px;
        padding-right: 5px;
        padding-left: 5px;
        font-size: 12px;
    } 
}