.search-container {
    width: 95%;
    height: 70px;
    margin: 0 auto;
    display: flex;
    background: white;
    justify-content: space-between;
    align-items: center; 
}
.search-content {
    position: relative;
}
.search-img {
    position: absolute;
    top: 30%;
    width: 15px;
    right: 4%;
}
.search {
    width: 300px;
    height: 40px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #D8DCE6;
    border-radius: 4px;
    opacity: 1;
    outline: none;
    text-indent: 10px;
} 
.select-box {
    width: 250px;
    height: 40px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #D8DCE6 !important;
    border-radius: 4px;
    opacity: 1;
    text-indent: 10px; 
} 
.select-box li{
    font-size: 14px !important; 
}
.select-box:hover {
    border: 1px solid #D8DCE6 !important;
}
@media only screen and (max-width:800px) { 
    .search-content {
        
    }
    .search {
        width: 250px;
    }
    .select-box {
        width: 200px;
    }
}
@media only screen and (max-width:700px) {
    .select-box{
        display: none !important;
    }
    .search-content {
        width: 90%;
        margin: 0 auto;
    }
    .search {
        width: 100%;
    }
}