
.mobile-navbar {
    display: none;
}
.desktop-navbar {
    display: flex; 
    width: 100%;
}
.nav-top-bar {
    height: 70px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    opacity: 1;
} 
.nav-logo {
    width: 100px;
    margin: 0px 20px;
} 
.side-bar {
    width: 100%; 
    height: 100%;
    background: white;
}
.content-bar {
    width: calc(100vw - 250px); 
}
.side-bar-elements {
    margin: 20px;
    width: calc(100% - 20px);
    display: flex;
    flex-direction: column;
    height: calc(100% - 40px); 
}
.side-bar-ele {
    width: 100%;
    opacity: 1;
    margin: 2px 0px;
    font-size: 13px;
    padding: 15px 0px;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #000;
    border-radius: 2px;
}
.side-bar-icon {
    margin: 0px 10px;
    width: 20px;
}
.side-bar-ele-active {
    color: #0F5EEA;
    background: #0F5EEA1C 0% 0% no-repeat padding-box;
} 
.top-bar-left {
    height: 70px;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-left-logo {
    height: 50%;
}
.drawer div:nth-child(3){
    width: 200px;
}

@media only screen and (max-width: 700px) {
    .desktop-navbar {
        display: none;
    }
    .navbar-container {
        width: 100%;
    }
    .mobile-navbar {
        display: flex;
    }
    .side-bar {
        width: 95%;
        margin: 0 auto;
    }
    .top-bar-left {
        justify-content: flex-start;
    }
    .menu-icon {
        padding: 5px;
    }
    .nav-left-logo {
        height: 25px; 
    }
    .side-bar-elements {
        width: 85%;
    }
}