.back-bar {
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
}
.back-icon {
    position: relative;
    transform: rotateY(180deg);
}
.back-bar div {
    font-size: 14px;
    color: #333;
}

@media only screen and (max-width: 600px) {
    .back-bar {
        height: 40px;
    }
}