.basic-info {
    width: 90%;
    margin: 10px auto; 
}
.basic-info-content {
    padding: 15px 0px;
}
.basic-info-content h4 {
    padding: 10px 0px;
    opacity: 0.8;
    font-size: 15px;
}
.basic-conf-head {
    padding: 10px 0px;
    font-size: 13px;
    opacity: 0.9;
}
.form-div {
    display: flex;
    align-items: center; 
    width: 100%;
    justify-content: flex-start;
}
.form-div label {
    flex: 2;
}
.form-div button{
    flex: 7; 
    display: flex;
    margin-left: 15px;
    justify-content: flex-start;
}

@media only screen and (max-width: 700px) {
    .basic-info {
        width: 96%;
    }
}