.generate-invoice {
    width: 1000px; 
    margin: 20px 0px;
    height: auto; 
}
.generate-invoice .invoice-head {
    padding: 10px 0px; 
}
.generate-invoice .invoice-head h2 {
    color: rgb(134, 134, 134);
    font-size: 16px;
}
.generate-invoice .invoice-head h2 span {
    color: #333;
} 
.generate-invoice .invoice-content .invoice-hint-text{
    display: flex;
    align-items: center;
    height: 70px;
    padding: 0px 10px;
    background: #FF601E0F;
    font-size: 13px;
}
.generate-invoice .invoice-content .invoice-hint-text img{
    width: 15px;
}
.generate-invoice .invoice-content button {
    margin-bottom: 20px;
}
@media only screen and (max-width: 1020px) {
    .generate-invoice {
        width: 95%;
    }
}