.product-conf {
    width: 100%;
    height: 100vh;
}
.container-one {
    width: 95%;
    margin: 0 auto;
}
.product-conf-top-bar {
    height: 90px; 
    width: 100%;
    box-shadow: 1px 1px 20px rgb(0, 0, 0,0.1);
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    margin: 0 auto;
}
.product-conf-img {
    width: 100px;
    margin-top: 10px;
    margin-left: 20px;
}
.product-conf-tabs {
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center; 
}
.product-conf-content {
    height: calc(100vh - 100px);
    width: 100%; 
    margin-top: 10px;
    overflow-y: scroll;
}
.product-conf-row-1 {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;  
}
.product-conf-row-1 {
    /* background:  */
}

@media only screen and (max-width: 700px) {
    .product-conf-img {
        display: none;
    }
    .product-conf-top-bar{
        height: 60px;
        box-shadow: none;
        width: 100%;
    }
}