.main-image {
    width: 100%;
}
.main-image h5 {
    text-transform: uppercase;
    opacity: 0.9;
}
.main-image-container {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
}
.main-img-content {
    width: 100px;
    margin: 20px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;
    background: #F0F2F8 0% 0% no-repeat padding-box;
}
.main-img-content .add-image-icon {
    width: 25px;
    margin-bottom: 10px;
}
.main-img-content p {
    font-size: 10px;
    opacity: 0.8;
}
.main-img-content span {
    position: absolute;  
    top: -2px;
    right: -2px;   
    background: red;
    color: white;
    border-radius: 100px;
    padding: 0px 5px;                                                                                                                   
}
.main-img-content span:hover {
    cursor: pointer;
}
.main-multi-img-input {
    width: 100px;
    height: 100px;
    position: absolute;
    opacity: 0;
}
.main-multi-img-input:hover {
    cursor: pointer;
}
.main-img-content .main-img-content-img {
    max-width: 90px;
    max-height: 90px;   
}
.main-img-content-border {
    border: 1px solid rgba(51, 51, 51, 0.2);
    border-radius: 4px;
}