* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none !important;
} 
.global-container {
    display: flex;
    width: 100vw;
}
.navbar-container {
    width: 240px !important; 
    background: rgba(15, 95, 234, 0.050);  
    border-right: 1px solid rgba(51, 51, 51, 0.01);
}
.content-container { 
    width: 100%;
    height: calc(100vh - 70px);
    display: flex;
    flex-direction: row;
}
::-webkit-scrollbar {
    width: 6px !important;
}
::-webkit-scrollbar-track {
    background: #f1f1f1;
} 
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
} 
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
@media only screen and (max-width: 850px) {
    .navbar-container {
        width: 200px !important;
    }
    .content-container { 
        flex: 1;
    }
}
@media only screen and (max-width: 700px) {
    .global-container {
        flex-direction: column;
    }
    .content-container {
        width: 100vw !important;
    }
    ::-webkit-scrollbar { 
        display: none;
    }
}