.main-container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.btn-text {
    color: #0F5EEA;
    font-size: 14px;
    outline: none;
    border: none;
    background: transparent;
    padding: 10px 15px;
}
.add-product-nav {
    width: 350px;
    padding: 0px 10px;
}
.add-product-nav .add-product-nav-content {
    height: 60px;
    display: flex; 
    align-items: center;
    border-bottom: 1px solid rgba(119, 119, 119, 0.2);
}
.add-product-nav .add-product-nav-content img {
    width: 18px;
    margin-right: 15px; 
}

@media only screen and (max-width: 400px) {
    .add-product-nav {   
        width: 280px;
    }
}