.order-details {
    display: flex;
    flex-direction: column;
    height: calc(100vh - 120px); 
    width: 100%;
    overflow-y: scroll;
}
.order-details h1 {
    font-size: 16px;
    color: #333;
    margin: 20px 0px;
}
.order-details .o-d-row-1 {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    width: 98%;
    align-items: flex-start;
    flex-direction: row;
} 
.order-details .o-d-row-1 .order-card-one {
    margin-left: 0px; 
    margin-right: 2%;
    width: 55%;
}
.order-details .o-d-row-1 .order-card-two { 
    margin-right: 0px;
    margin-left: 2%;
    width: 43%;
}

.order-details .order-product-info { 
    padding: 20px;
    display: flex;
    align-items: flex-start;
}
.order-details .order-product-info img {
    width: 70px;
    height: auto;
    margin-right: 10px;
}
.order-details .order-product-info .order-product-info-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-start;
} 
.order-details .order-product-info .order-product-info-content h2 {
    font-size: 14px;
    color: rgb(104, 104, 104);
    margin-bottom: 5px;
}
.order-details .order-product-info .order-product-info-content p {
    display: flex;
    font-size: 13px;
    margin: 6px 0px;
}
.order-details .order-product-info .order-product-info-content p span:nth-child(1) {
    flex: 1;
    color: rgb(139, 139, 139);
}
.order-details .order-product-info .order-product-info-content p span:nth-child(2) {
    flex: 2;
}
.btn-outline-red {
    color: #FF0000;
    width: 120px;
    font-weight: 500;
    border-color: #FF0000;
    margin: 10px 0px;
}
.order-details .o-d-row-1 .order-product-invoice {
    height: auto;
}
.order-details .o-d-row-1 .order-product-invoice h2{
    display: flex;
    align-items: center;
    font-size: 14px;
    padding: 15px;
}
.order-details .o-d-row-1 .order-product-invoice h2 img {
    width: 15px;
    margin-left: 5px;
}
.order-details .o-d-row-1 .order-product-invoice p {
    font-size: 13px;
    padding: 15px;
    display: flex;
    align-items: center;
    color: #0F5EEA;
}
.order-details .o-d-row-1 .order-product-invoice p img {
    width: 15px;
    margin-right: 5px;
}
.order-details .order-product-buyer {
    width: 600px; 
    margin: 20px 0px; 
}
.order-details .order-product-buyer-content {
    padding: 25px;
}
.order-details .order-product-buyer-content h2 {
    font-size: 15px;
}
.order-details .order-product-buyer-content p {
    font-size: 14px;
    margin: 10px 0px;   
}
.order-details .order-product-billing {
    width: 600px;
    margin: 20px 0px;
}
.order-details .order-product-billing-content {
    padding: 25px;
}
.order-details .order-product-billing-content .topbar {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}
.order-details .order-product-billing-content .topbar h2 {
    font-size: 15px;
}
.order-details .order-product-billing-content .topbar p {
    font-size: 14px;
    color: rgb(87, 87, 87);
}

.order-details .order-product-billing-content .topbar p span {
    color: #00C569;
}

.order-details .order-product-billing-content p {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 15px 0px;
    color: #333;
    font-size: 14px;
}
.order-details .order-product-billing-content p span:nth-child(2) {
    color: #000;
}
.order-details .order-product-billing-content .total {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
}

@media only screen and (max-width: 1000px) {
    .order-details .order-product-info .order-product-info-content p span:nth-child(2) {
        flex: 1;
    }
}
@media only screen and (max-width: 850px) {
    .order-details .o-d-row-1 {
        flex-direction: column; 
    }
    .order-details .o-d-row-1 .order-card-one {
        width: 98%;
        margin: 0 auto;
    }
    .order-details .o-d-row-1 .order-card-two {
        width: 98%;
        margin: 10px auto;
    }
    .order-details .order-product-buyer {
        width: 98%;
        margin: 10px auto;
    }
    .order-details .order-product-billing {
        width: 98%;
        margin:  0 auto;
    }
}