
.content-nav-bar {
    height: 60px;
    background: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 95%;
    margin: 0 auto; 
}
.content-nav-bar-items { 
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;    
}
.content-nav-bar-items .content-nav-bar-item {
    height: 100%; 
    display: flex;
    align-items: center; 
    font-size: 13.5px;
    padding: 0px 15px;
}
.content-nav-bar-item:hover {
    cursor: pointer;
}
.content-nav-bar-item-active {
    color: #0F5EEA;
    border-bottom: 2px solid #0F5EEA;
}
.content-nav-bar-btn {
    background: #0F5EEA 0% 0% no-repeat padding-box;
    box-shadow: 0px 2px 4px #0000004D;
    border-radius: 4px;
    opacity: 1;
    color: white;
    font-size: 14px;
    outline: none;
    border: none; 
    padding: 10px 20px;
} 
.desktop-view-nav {
    display: flex;
}
@media only screen and (max-width: 1200px) {
    .app-bar-tabs div div div button {
        min-width: 100px;
        padding: 10px 10px;
    } 

    .app-bar-tabs {
        width: 600px;
    }
    .app-bar-tabs div div div button span {
        font-size: 14px;
    }
}
@media only screen and (max-width: 900px) {
    .app-bar-tabs {  
        width: 500px
    }
    .app-bar-tabs div div div button {
        min-width: 120px;
    }
}
@media only screen and (max-width: 800px) {
    .app-bar-tabs {  
        width: 400px
    }
    .app-bar-tabs div div div button {
        min-width: 120px;
    }
}
@media only screen and (max-width: 700px) {
    .desktop-view-nav {
        display: none;
    }
    .app-bar-tabs {
        width: 100%;
        overflow-x: scroll;
    }
    .content-nav-bar  {
        height: 40px;
        width: 100%;
    }
}
