
.order-content { 
    height: auto;
    padding: 5px 0px;
    width: 100%;
    margin: 10px 0px;
    display: flex; 
    flex-direction: column;
    box-shadow: 0px 0px 4px #00000038;
    border-radius: 4px;
    background: white;
    opacity: 1;
}
.order-content:nth-child(1) {
    margin-top: 5px;
}
.order-col-1 {
    margin: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex: 1;
    margin-bottom: 0;
}
.order-content .order-head p {
    font-size: 13px;
    color: #333;
    margin-top: 5px;
}
.order-content .order-status {
    font-size: 13px;
    opacity: 0.9;
    color: #333;
}
.order-content .order-status span {
    color: #000;
}

.order-content .order-head p span {
    color: #FF601E;
}
.order-content .order-img {
    width:60px;
    height: 60px;
    margin: 0px 5px;
    margin-right: 10px;
    border: 1px solid rgb(228, 228, 228);
    border-radius: 2px;
    padding: 5px;
}
.order-col-2 {
    display: flex;
    flex: 15; 
    margin: 10px;  
    flex-direction: column; 
    justify-content: space-between;
}
.order-row-1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    align-items: center;
}
.order-row-1 h5 {
    font-weight: 500;
}
.order-row-2 {
    display: flex;
    padding: 15px 0px;
    margin-top: 10px;
    justify-content: space-between;
    align-items: center;
}
.order-row-2-content {
    display: flex;
    flex-direction: row;
}
.order-spec {
    width: 150px; 
    padding: 0px 10px;
    align-items: center;
    border-right: 1px solid rgba(51, 51, 51, 0.2);
}
.order-spec:nth-child(1) {
    padding-left: 0px;
}
.order-spec:last-child {
    border: none;
}
.order-key  {
    font-size: 12px; 
    display: flex;
    color: rgb(107, 107, 107);
}
.order-key:nth-child(1) {
    margin-bottom: 10px;
}
.order-key span:nth-child(1) {
    flex: 1;
}
.order-key span:nth-child(2) {
    flex: 1;
    color: #000;
}
.order-value {
    font-size: 12px; 
}
.order-row-2-buttons {
    display: flex;
} 

@media only screen and (max-width: 1100px) {
    .btn-outline,
    .btn-primary {
        padding: 12px 10px;
    }
    .btn-outline {
        padding: 10px 10px;
    }
    .order-row-2 {
        padding: 0px;
        margin: 0;
    }
    .order-row-2-buttons {
        display: flex;  
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-end;
    }
    .order-row-2-buttons button {
        margin: 5px 0px;
        width: 140px;
    }
    .product-content {
        padding: 15px 0px;
    }
    .product-spec {
        width: 80px;
    }
}
@media only screen and (max-width: 1000px) {
    .order-spec {
        width: 150px;
    }
    .order-col-2 {
        margin-left: 20px;
    }
}
@media only screen and (max-width: 900px) {
    .product-content {
        padding: 10px 0px;
    }
    .order-col-2 {
        margin: 0px;
    }
    .order-col-1 {
        margin-left: 5px;
        margin-right: 5px;
    }
    .product-spec {
        width: 70px;
        margin-right: 10px;
        border-right: 1px solid rgba(51, 51, 51, 0.2);
    }
    .order-col-1 span {
        padding-right: 0px;
        padding-left: 0px;
    }
    .order-col-1 {
        margin: 10px 10px;
    }
    .order-row-2 {
        margin: 0px 10px;
    }
    .order-row-2-buttons button {
        margin: 5px 0px;
        width: 120px;
    }
}
@media only screen and (max-width: 850px) {
    .order-spec:nth-child(3) {
        display: none;
    }
}  
@media only screen and (max-width: 500px) {
    .order-content .order-status {
        display: none;
    }
    .order-row-2-content {
        margin: 10px 0px;
    }
    .order-row-2 {
        flex-direction: column;
        align-items: flex-start;
    }
    .order-row-2-buttons {
        flex-direction: row;
        margin-top: 10px;
        align-self: flex-end;
    }
    .order-row-2-buttons button {
        margin: 0px 5px;
    }
}

@media only screen and (max-width: 400px) {
    .order-row-2-content {
        width: 100%;
    }
    .order-spec {
        width: 50%;
    }
}