.expansion-icon {
    transform: rotateZ(90deg);
}
.expansion-icon-close {
    transform: rotateZ(270deg);
}
.add-existing-product-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 60px;
}
.add-existing-product-header .product-content-left {
    display: flex;
    align-items: center;
}
.add-existing-product-header .product-header-img {
    width:  80px;
}
.add-existing-product-header .product-header-img img {
    width: 70px;
    padding: 10px;
    border-radius: 4px; 
    border: 1px solid rgb(218, 218, 218);
}
.add-existing-product-header .product-header-head h1 {
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0px;
    color: #000000;
    text-transform: capitalize;
    opacity: 0.7;
}
.add-existing-product-header .product-header-head .product-header-div {
    display: flex;
    align-items: center;
}
.add-existing-product-header .product-header-head .product-header-div p {
    font-size: 12px;
    margin-right: 10px;
    width: 150px;
    letter-spacing: 0.2px;
    color: rgb(112, 112, 112);
    margin-top: 8px;
}
.add-existing-product-header .product-header-head p span {
    color: #000;
    margin-left: 4px;
}
.add-existing-product-header .button-accordian {
    display: flex;
    align-items: center; 
}
.add-existing-product-header .button-accordian img {
    height: 15px;
}
.add-existing-product-content  {
    display: flex;
    flex-direction: column;
    width: 95%;
    margin-left: auto;
}
.add-existing-product-content .add-variant-store-content {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}
.add-existing-product-content .add-variant-store-content h2 {
    font-size: 15px;
    color: rgb(41, 41, 41);
    margin-left: 20px;
}
.add-existing-product-content .add-variant-store-content button {
    margin-right: 20px;
}
@media only screen and (max-width: 650px) {
    .add-existing-product-header .product-header-head h1 {
        font-size: 13px;
    } 
    .add-existing-product-header {
        flex-direction: column;
        height: auto;
        align-items: flex-start;
    }
    .add-existing-product-header .button-accordian  {
        align-self: flex-end;
        margin-top: 20px;
        margin-right: 0;
    }
}