.business-detail {
    margin: 20px auto 
}
.business-detail .business-detail-content {
    padding: 20px;  
}
.business-detail .business-detail-content h1 {
    font-size: 16px;
    color: #333;
}
.business-detail .business-detail-content .business-detail-input-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: 20px 0px;
}
.business-detail .business-detail-content .business-detail-input-container div  {
    width: 30%;
}
.business-detail .business-detail-content .business-detail-input-container div:nth-child(1) {
    margin-right: 2%;
}
.business-detail .business-detail-content .business-detail-input-container div:nth-child(2) {
    margin: 0px 3%;
}
.business-detail .business-detail-content .business-detail-input-container div:nth-child(3) {
    margin-left: 2%;
}
.business-detail .business-detail-content .business-detail-input-container div:nth-child(4) {
    margin-right: 2%;
}
.business-detail .business-detail-content .business-detail-input-container div:nth-child(5) {
    margin: 0px 3%;
}
.business-detail .business-detail-content .business-detail-input-container div:nth-child(6) {
    margin-left: 2%;
}
.business-detail .business-detail-input-container .delivery-service {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.business-detail-input-container .delivery-service p {
    font-size: 13px;
}
.business-detail-input-container .delivery-service .radio-content {
    display: flex;
    margin: 0px !important;
    align-items: center;
}
.business-detail-input-container .delivery-service .radio-content div{
    display: flex;
    align-items: center;
    width: auto;
    margin-right: 10px;
}
.business-detail-input-container .delivery-service .radio-content div label {
    font-size: 13px;
}
.business-detail-input-container .delivery-service .radio-content div span {
    padding: 5px;
}

.business-detail-input-container .add-tags {
    width: 100% !important; 
    margin: 0 !important;
    padding-top: 20px;
    position: relative;
}
.business-detail-input-container .add-tags .chips {
    position: absolute;
    width: 100%;
}
.business-detail-input-container .add-tags .chips div {
    width: 75px;
    margin: 0px 5px !important;
}
.business-detail-input-container .add-tags input {
    font-size: 13px;
    height: 35px;
    width: 100%;
    border-radius: 2px;
    outline: none;
    text-indent: 10px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #00000033;
    border-radius: 4px;
    opacity: 1;
}

@media only screen and (max-width: 950px) {
    .business-detail .business-detail-content .business-detail-input-container .delivery-service {
        width:48%;
        margin: 5px 0px !important;
    }
    .business-detail .business-detail-content .business-detail-input-container {
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .business-detail .business-detail-content .business-detail-input-container .business-detail-input-content  {
        width:48%;
        margin: 5px 0px !important;
    }
}
@media only screen and (max-width: 750px) {

    .business-detail .business-detail-content .business-detail-input-container .delivery-service { 
        width: 98%;
        margin:  0 auto !important;
    }
    .business-detail .business-detail-content .business-detail-input-container .business-detail-input-content  {
        width: 98%;
        margin: 0px auto !important;
    }
}