.form-table-input,
.form-table-select { 
    width: 60px;
    height: 30px;
    border: 1px solid rgb(204, 204, 204);
    outline: none;
    text-indent: 5px;
    background: white;
    border-radius: 2px;
}
.form-table-select option{
    background: white;
    border: 1px solid red;
    height: 100px !important;
}
