
.modal-md-content {
    width: auto;
    height: auto;
    top: 50%;
    padding: 30px;
    border-radius: 10px;
    outline: none;
    left: 50%; 
    transform: translate(-50%,-50%);
    position: absolute;
    background: white;
}
.modal-md-content .modal-close {
    top: 1%;
    right: 1%;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    position: absolute;
}
.modal-md-content .modal-close:hover {
    cursor: pointer;    
}
