.address-detail {
    margin: 20px auto 
}
.address-detail .address-detail-content {
    padding: 20px;  
}
.address-detail .address-detail-content h1 {
    font-size: 16px;
    color: #333;
}
.address-detail .address-detail-content .address-detail-input-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: 20px 0px;
}
.address-detail .address-detail-content .address-detail-input-container div  {
    width: 30%;
}
.address-detail .address-detail-content .address-detail-input-container div:nth-child(1) {
    margin-right: 2%;
}
.address-detail .address-detail-content .address-detail-input-container div:nth-child(2) {
    margin: 0px 3%;
}
.address-detail .address-detail-content .address-detail-input-container div:nth-child(3) {
    margin-left: 2%;
}
.address-detail .address-detail-content .address-detail-input-container div:nth-child(4) {
    margin-right: 2%;
}
.address-detail .address-detail-content .address-detail-input-container div:nth-child(5) {
    margin: 0px 3%;
}
.address-detail .address-detail-content .address-detail-input-container div:nth-child(6) {
    margin-left: 2%;
}

@media only screen and (max-width: 950px) {
    .address-detail .address-detail-content .address-detail-input-container {
        flex-wrap: wrap;
        margin: 0px 0px;
        justify-content: space-between;
    }
    .address-detail .address-detail-content .address-detail-input-container .address-detail-input-content  {
        width:48%;
        margin: 5px 0px !important;
    }
}
@media only screen and (max-width: 750px) {
    .address-detail .address-detail-content .address-detail-input-container .address-detail-input-content  {
        width: 98%;
        margin: 0px auto !important;
    }
}