.instruction {
    width: 100%;
    height: auto;
    margin: 15px 0px;
    padding: 20px;
    background: #FF601E0F 0% 0% no-repeat padding-box;
}
.ins-content {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.ins-content span img {
    width: 15px;
    margin-right: 10px;
}
.ins-content span {
    font-size: 13px;
    display: flex;
    margin: 5px 0px;
    align-items: center;
}
.image-error-text {
    margin: 0px 20px;
    color: red;
    font-size: 14px;
}