.otp-container {
    width: 500px;
}
.otp-container h2 {
    font-size: 16px;
    color: #333;
}
.otp-container span {
    font-size: 13px;
    color: rgb(128, 128, 128);
    margin-top: 10px;
}
.otp-container .text-error {
    color: red;
}
.form-phone {
    display: flex;
    width: 100%;
    align-items: center !important;
    flex-direction: row !important;
}
.form-group-otp {
    flex-direction: column;
    display: flex;
    align-items: flex-start;
    width: 100%;
}
.form-phone p {
    border: 1px solid rgb(206, 206, 206);
    border-radius: 4px;
    padding: 10px;
    font-size: 14px;
}
.otp-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
}