.cat-container {
    width: 95%;
    height: calc(100vh - 120px);
    overflow-y: scroll;
    margin: 20px auto;
}
.cat-head {
    font-size: 20px;
    color: #18191BF7;
    opacity: 8.6;
}
.cat-cards {
    width: 100%;
    margin: 20px 0px;
    display: flex;
    flex-wrap: wrap; 
    justify-content: flex-start;
}
.cat-card {
    width: 28%; 
    margin: 10px 0px;
    height: 140px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #979797;
    border-radius: 8px;
    opacity: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
} 
.cat-card:nth-child(1),
.cat-card:nth-child(4),
.cat-card:nth-child(7){
    margin-right: 2%;
}
.cat-card:nth-child(2),
.cat-card:nth-child(5),
.cat-card:nth-child(8){
    margin:10px 5%;
} 
.cat-card:nth-child(3),
.cat-card:nth-child(6),
.cat-card:nth-child(9){
    margin-left: 2%;
} 
.cat-card h4 {
    letter-spacing: 0.26px;
    color: #000000;
    opacity: 1;
}
.cat-card p {
    margin: 6px 0px;
    font-size: 12px;
    letter-spacing: 0.16px;
    color: #000000;
    opacity: 0.54;
}
.cat-card span {
    letter-spacing: 0px;
    color: #0F5EEA;
    margin-top: 20px;
    text-transform: uppercase;
    opacity: 1;
    font-size: 14px;
}

.modal-content {
    width: 450px;
    padding: 20px;
}
.modal-content h1{
    font-size: 16px;
    margin-bottom: 10px;
}
.modal-col-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
    margin: 5px 0px;
    border-bottom: 1px solid rgba(187, 187, 187, 0.4);
}
.modal-col-content p {
    font-size: 14px;
}
.modal-cat-name {
    font-size: 15px;
    padding: 5px 0px;
    padding-bottom: 15px;
}
.btn-container-center {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 10px 0px;
}

@media only screen and (max-width: 1000px) {
    .cat-card {
        width: 45%;
        margin: 10px !important;
    }
    .cat-cards {
        justify-content: space-between;
    }
}
@media only screen and (max-width: 550px) {
    .cat-card {
        width: 90%;
        margin: 10px auto !important;
    }
    .cat-cards {
        justify-content: center;
    }
    .cat-head {
        font-size: 16px;
        width: 90%;
        margin: 0 auto;
    }
    .modal-content {
        width: 300px;
    }
}