.crop-image {
    width: 90vw;
    height: 80vh;
    padding: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.crop-image-container {
    display: flex; 
    width: 40vw; 
    position: relative;
    height: 40vw;
    align-items: center;
    justify-content: flex-start;
} 
.cropped-image-container {
    width: 30vw;
    height: 30vw;
    padding: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.cropped-image-container button {
    align-self: flex-end;
}
.cropped-image-container img {
    width: 30vw;
    height: 30vw; 
    background-image: url('../../../../assets/img/bg.png'); 
    margin: 20px 0px;
    padding: 20px;
}
.crop-image-loading-container {
    position: absolute;
    width: 100%;
    height: 100%;
    flex-direction: column;
    display: flex;
    align-items: center;
    z-index: 1000;
    background: rgba(255, 255, 255, 0.7);
    justify-content: center;
}
.crop-image-loading-container span {
    font-size: 13px;
    margin-top: 20px;
}